import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Collapse from 'antd/lib/collapse';
import Row from 'antd/lib/row';
import Col from 'antd/lib/col';
import Select from 'antd/lib/select';
import Icon from 'antd/lib/icon';
import Tooltip from 'antd/lib/tooltip';
import AutoComplete from './common/AutoComplete';
import RowHelp from './common/RowHelp';
import BoxModel from './common/BoxModel';
import { getOption } from '../utils';

const Panel = Collapse.Panel;

export default class EditorInterface extends Component {
  static propTypes = {
    className: PropTypes.string,
    header: PropTypes.string,
    value: PropTypes.object,
    onChange: PropTypes.func,
  };

  static defaultProps = {
    className: 'editor-font',
    header: '',
    value: {
      overflow: 'visible',
      width: '0px',
      maxWidth: null,
      minWidth: null,
      position: 'static',
      top: null,
      right: null,
      bottom: null,
      left: null,
    },
    onChange: () => { },
  };

  onChange = (key, v) => {
    const value = key === 'offset' ? {
      ...this.props.value,
      ...v,
    }
      : {
        ...this.props.value,
        [key]: v,
      };
    this.props.onChange('interface', value);
  }

  pos = { static: '', absolute: '', relative: '', fixed: '' };

  overflow = { visible: '', hidden: '', scroll: '', auto: '' };

  render() {
    const { ...props } = this.props;
    const { value } = props;
    ['value', 'font'].map(key => delete props[key]);
    return (
      <Panel {...props}>
        <Row gutter={8}>
          <Col span={4}>
            
          </Col>
          <Col span={20}>
            <Select
              style={{ width: '100%' }}
              value={value.overflow || 'visible'}
              size="small"
              onChange={(e) => {
                this.onChange('overflow', e);
              }}
              dropdownMatchSelectWidth={false}
              dropdownClassName="editor-list-dropdown"
            >
              {getOption(this.overflow)}
            </Select>
          </Col>
        </Row>
        <Row gutter={8}>
          <Col span={4}>
            
          </Col>
          <Col span={8}>
            <AutoComplete
              style={{ width: '100%' }}
              value={value.width}
              onChange={(e) => {
                this.onChange('width', e);
              }}
            />
          </Col>
          <Col span={6}>
            <AutoComplete
              style={{ width: '100%' }}
              placeholder="min"
              value={value.minWidth}
              onChange={(e) => {
                this.onChange('minWidth', e);
              }}
            />
          </Col>
          <Col span={6}>
            <AutoComplete
              style={{ width: '100%' }}
              placeholder="max"
              value={value.maxWidth}
              onChange={(e) => {
                this.onChange('maxWidth', e);
              }}
            />
          </Col>
        </Row>
        <Row gutter={8}>
          <Col span={4}>
            
          </Col>
          <Col span={8}>
            <AutoComplete
              style={{ width: '100%' }}
              value={value.height}
              onChange={(e) => {
                this.onChange('height', e);
              }}
            />
          </Col>
          <Col span={6}>
            <AutoComplete
              style={{ width: '100%' }}
              placeholder="min"
              value={value.minHeight}
              onChange={(e) => {
                this.onChange('minHeight', e);
              }}
            />
          </Col>
          <Col span={6}>
            <AutoComplete
              style={{ width: '100%' }}
              placeholder="max"
              value={value.maxHeight}
              onChange={(e) => {
                this.onChange('maxHeight', e);
              }}
            />
          </Col>
        </Row>
        <RowHelp title="" help={<div>
          </div>}
        >
          <Select
            style={{ width: '100%' }}
            value={value.position || 'static'}
            size="small"
            onChange={(e) => {
              this.onChange('position', e);
            }}
            dropdownMatchSelectWidth={false}
            dropdownClassName="editor-list-dropdown"
          >
            {getOption(this.pos)}
          </Select>
        </RowHelp>
        <Row >
          <Col>
             - Position
            <Tooltip
              placement="topRight"
              arrowPointAtCenter
              title={<span>1. ; <br /> 2.  4  top left ;</span>}
            >
              <Icon type="question-circle" style={{ marginLeft: 5 }} />
            </Tooltip>
          </Col>
        </Row>
        <BoxModel keys={['top', 'right', 'bottom', 'left']}
          value={{
            top: value.top,
            right: value.right,
            bottom: value.bottom,
            left: value.left,
          }}
          onChange={(e) => {
            this.onChange('offset', e);
          }}
          disabled={!value.position || value.position === 'static'}
        />
      </Panel>
    );
  }
}

EditorInterface.componentName = 'EditorInterface';



