import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Collapse from 'antd/lib/collapse';
import Radio from 'antd/lib/radio';
import Row from 'antd/lib/row';
import Col from 'antd/lib/col';
import Select from 'antd/lib/select';
import { getOption } from '../utils';

const Panel = Collapse.Panel;

const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;

const cursorState = {
  auto: '',
  pointer: '',
  crosshair: '',
  move: '',
  text: '',
  wait: '',
  help: '',
};

export default class EditorState extends Component {
  static propTypes = {
    value: PropTypes.object,
    onChange: PropTypes.func,
    header: PropTypes.string,
    showClassState: PropTypes.bool,
  };

  static defaultProps = {
    header: '',
    value: '',
    onChange: () => {
    },
  };

  render() {
    const { ...props } = this.props;
    const { value, showClassState, isMobile } = props;
    ['value', 'onChange'].map(key => delete props[key]);
    const childrenToRender = [
      { value: 'default', content: '' },
      { value: 'hover', content: '' },
      { value: 'active', content: '' },
      { value: 'focus', content: '' },
    ].map(item => {
      if (isMobile && item.value === 'hover') {
        return null;
      }
      return (
        <RadioButton value={item.value} key={item.value} className="ant-radio-button-auto-width">
          {item.content}
        </RadioButton>
      );
    }).filter(c => c);
    return (<Panel {...props} >
      <Row gutter={8}>
        <Col span={24}>
          
        </Col>
      </Row>
      <Row gutter={8}>
        <Col span={24}>
          <Select
            style={{ width: '100%' }}
            value={value.cursor || 'auto'}
            size="small"
            onChange={(e) => {
              this.props.onChange('cursor', e);
            }}
            dropdownMatchSelectWidth={false}
            dropdownClassName="editor-list-dropdown"
          >
            {getOption(cursorState)}
          </Select>
        </Col>
      </Row>
      {showClassState && [
        <Row gutter={8} key="0">
          <Col span={24}>
            
          </Col>
        </Row>,
        <Row gutter={8} key="1">
          <Col span={24}>
            <RadioGroup
              defaultValue={value.classState}
              size="small"
              onChange={(e) => {
                this.props.onChange('classState', e.target.value);
              }}
            >
              {childrenToRender}
            </RadioGroup>
          </Col>
        </Row>]}
    </Panel>);
  }
}

EditorState.componentName = 'EditorState';



